<template>
  <div class="swiper-slide">
       <music></music>
       <div class="container">
           <div class="snow">
                <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1447/de66a4add15d14c6c657dc018595d056.gif"/>
            </div>
           <div class="left animated zoomInLeft">
               <div class="bg"></div>
               <div class="star1">
                   <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/74bee8f9c9c3d3b90f8bce4709e38d39.png?x-oss-process=image/resize,w_200"/>
               </div>
               <div class="star2">
                   <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/74bee8f9c9c3d3b90f8bce4709e38d39.png?x-oss-process=image/resize,w_200"/>
               </div>
           </div>
           <div class="right">
               <div class="info">
                   <div class="info-info animated bounceInDown">
                       <div class="text-render animated fadeInDownBig">T<br>H<br>E<br> G<br>R<br>O<br>O<br>M</div>
                   </div>
                   <div class="userinfo animated fadeInLeftBig">新<br>/<br>娘</div>
                   <div class="groom animated fadeInLeftBig"><img class="bounceIn animated" src="~@/assets/stars.png" >林小姐</div>
                   <div class="juzhi animated lightSpeedIn">执子之手 <br>与子偕老</div>
               </div>
               <div class="bottom animated bounceIn">
                   <div class="wrap"> <img src="https://res.zhangu365.com/fodder/designer/img/2019/0927/1457/2d87cffe7be4b746447b061f5ed81c2b.jpg?x-oss-process=image/resize,w_300"></div>
               </div>
           </div>
       </div>
       <div class="masklogo"></div>
       <div class="footLogo animated bounceInRight">
           -WEDDING &nbsp;&nbsp;INVITATION-
       </div>
  </div>
</template>

<script>
import music from "../template/music";
export default {
  name: "page3",
  data() {
    return {};
  },
  components:{music}
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";
.swiper-slide {
  position: relative;
  width: 100%;
  height: 100%;
  //padding: 2.975rem 0 5.1271rem 0;
  box-sizing: border-box;
  .container{
      position: absolute;
      top: 2.975rem;
      right: 0;
      bottom: 5.1271rem;
      left: 0;
      
      @include wh(100%,calc(100% - 8.1021rem));
      @include fj();
      .snow{
          @include pos(1.15rem, 0);
          @include wh(16.45rem, 16.45rem);
          margin: 0 auto;
          z-index: 1;
          img {
              width: 100%;
              object-fit: cover;
          }
      }
      .left{
          position: relative;
          @include wh(50%,100%);
          box-shadow:0.25rem 0.25rem 0.3rem -0.2rem rgba(0,0,0, 0.2),0.25rem -0.25rem 0.3rem -0.2rem  rgba(0,0,0, 0.2);
          padding: 0.5rem 0.5rem 0.5rem 0;
          overflow: hidden;
          box-sizing: border-box;
          animation: 0.6s ease 0s 1 normal both running bounceIn;
          
          .bg{
              $url:'https://res.zhangu365.com/fodder/designer/img/2019/0927/1501/a1eb206cb853b7fe57be52a923a07a56.jpg';
              @include bis($url);
              @include wh(100%,100%);
              background-size: cover;
              background-position: top;
              animation: 0.6s ease 0s 1 normal both running zoomInLeft;
          }
          .star1{
              @include wh(3.3985rem,2.2315rem);
              @include pos(1.05rem,1.3rem);
              animation: 0.4s ease 0s infinite normal both running bounceIn;
              img{
                width: 100%;
                object-fit: cover;
              }
          }
          .star2 {
            @include wh(3.3985rem,2.2315rem);
            position: absolute;
            bottom: 1.05rem;
            right: 1.3rem;
            animation: 0.4s ease 2s infinite normal both running bounceIn;
            img{
                width: 100%;
                object-fit: cover;
             }
           }
      }
      .right{
          position: relative;
          @include wh(50%,100%);
          .info{
              position: relative;
              padding: 1rem;
              box-sizing: border-box;
              .info-info{
                  position: relative;
                  @include wh(100%,11.3rem);
                  border: 0.2rem solid black;
                  box-sizing: border-box;
                  .text-render{
                       @include pos(0.4rem,0.4rem);
                    // @include pos(-1.5rem,2.7rem);
                    // transform: rotateZ(90deg);
                    color: rgb(14, 14, 14);
                    font-size: 0.71rem;
                    line-height: 1;
                    letter-spacing: 0px;
                    text-decoration: none;
                    font-family: "微软雅黑";
                    text-align: center;
                    animation: 0.6s ease 0.6s 1 normal both running fadeInDownBig;
                 }
                     animation: 0.4s ease 0s 1 normal both running bounceInDown;
              }
              .userinfo{
                  @include pos(4.7rem,1.5rem);
                  letter-spacing: 0rem;
                  text-align: center;
                  font-weight: 500;
                  font-size:1.4rem;
                  line-height: 1.1;
                  font-family: "微软雅黑";
                  animation: 0.6s ease 1.2s 1 normal both running fadeInLeftBig;
              }
              .groom{
                  @include pos(1.1rem,7.6rem);
                    @include font(1.1rem,1.5rem);
                    @include fj(center);
                    align-items: center;
                     animation: 0.6s ease 1.2s 1 normal both running fadeInLeftBig;
                    img{
                        width: 1.5rem;
                        height: 1.31rem;
                        line-height: 1.5rem;
                        margin: 0 0.4rem;
                       animation: 0.6s ease 2.8s infinite normal both running bounceIn;
                    }
                 }
                 .juzhi{
                     @include pos(0,9.2rem);
                     width: 100%;
                     text-align: center;
                     animation: 0.6s ease 1s 1 normal both running lightSpeedIn;
                 }
          }
          .bottom{
              @include wh(100%,100%);
              @include pos(0,0);
              padding: 14.3rem 0.9rem 0 0.9rem;
              box-sizing: border-box;
              animation: 0.6s ease 0s 1 normal both running bounceIn;
              .wrap{
                box-shadow: 0 0 0.3rem rgba(0,0,0, 0.2);
                padding: 0.2rem;
                box-sizing: border-box;
                @include wh(100%,100%);
                img{
                    @include wh(100%,100%);
                    object-fit: cover;
                }
              }
             
          }
      }
  }
  .footLogo{
      @include wh(100%,5.1271rem);
      @include fj(center);
      @include font(0.9rem,1);
      align-items: center;
      position: absolute;
      left: 0;
      bottom: 0;
      padding-bottom: 40px;
      letter-spacing: 0.15rem;
      animation: 0.6s ease 1.2s 1 normal both running bounceInRight;
  }
  .masklogo{
      $url:'https://res.zhangu365.com/fodder/designer/img/2019/0927/1439/7d4ce9206d68b13b51c4631ffd60db1d.png?x-oss-process=image/resize,w_500';
      @include bis($url);
      @include wh(14.2rem,4.3rem);
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -7.1rem;
      margin-top: -2.15rem;
      animation: 0.6s ease 2.2s 1 normal both running lightSpeedIn, 1s ease 3s infinite normal forwards running pulse;
  }
}
</style>


